.employeesList {
  box-shadow: 0 0 2px darkgray;
  width: 60%;
  color: rgba(0, 0, 0, 0.796);
  text-align: center;
  padding: 20px;
}

.employeesList__title {
  font-size: 28px;
  margin-bottom: 10px;
}

.employeesList__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
